<template>
    <div class="search">
        <div>
        <input class="search__input"
        v-model.trim="search"
        placeholder="Characters, comics, stories or series"
        @keyup.enter="getCharacters()"/>

        <button class="search__button"
        @click="getCharacters()">ASSEMBLE!
        </button>
        <p class="disclaimer">Due to limits of the Marvel API, it'll only display items that start with the letters A &amp; B</p>
        </div>
        <ul class="search__characters">
            <li v-for="character in characters" :key="character.id">
                    <router-link 
                    :to="{name: 'character', params: {id: character.id}}">
                    {{character.name}}</router-link>
            </li>
        </ul>
    </div>
</template>

<script>
import {api, public_key, hash} from '../marvel'
import axios from 'axios'

export default {
    name: 'Characters',

    data() {
        return {
            characters: [],
            search: "",
        }
    },

    methods: {
        getCharacters() {
            let one = `${api}?ts=1000&apikey=${public_key}&hash=${hash}&limit=60&offset=0`
            let two = `${api}?ts=1000&apikey=${public_key}&hash=${hash}&limit=60&offset=60`

            const requestOne = axios.get(one)
            const requestTwo = axios.get(two)

            axios.all([requestOne, requestTwo])
            .then(axios.spread((...responses) => {
                let responseOne = responses[0].data.data.results
                let responseTwo = responses[1].data.data.results

                // concatenar los arrays
                responseOne = responseOne.concat(responseTwo)
                // filtrar la respuesta
                if (this.search) {
                    this.characters = responseOne.filter(chars => 
                    chars.name.toLowerCase().startsWith(this.search.toLowerCase())
                    );
                } else {
                    // borrar lista si no hay nada en el input
                    this.characters = this.search
                }
            })).catch(errors => {
                console.log(errors)
            }) 
    }
},
}
</script>